.card{
    width: 10rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--colorCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 27px 3rem 27px;
}
.c-button{
    background: #ffffff;
    box-shadow: 0px 19px 60px rgba(0,0,0,0.08);
    border-radius: 7px;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 16px;
    color: #5290fd;
    font-weight: 500;
}
.cards{
    position: relative;
}
.cards > *{
    position: absolute;
}

@media screen and (max-width: 480px){

  

    .cards{
        display: flex;
        flex-direction: column;
        gap: 17.5rem;
    }
    .cards>*{
        position: static;
    }
    .card{
       width: auto;
    }


}